.template{
	position: absolute;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: start;
	    align-items: flex-start;
	height: 100%;
	width: 100%;
}

.back{
	height: 5%;
	position: relative;
	width: 100%;
	font-size: 1.1em;
	margin: 20px 0 5px 30px;
	text-align: left;
	padding-bottom: 10px;
}

span{
	overflow: hidden;
	display: inline-block;
}

.title{
	height: 15%;
	width: 100%;
	font-size: 5em;
	margin: 5px 0 30px 30px;
	text-align: left;
}

.placedate{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-direction: row;
	    flex-direction: row;
}

.emerge{
	-webkit-animation: 0.5s cubic-bezier(0.785, 0.135, 0.15, 0.86) emerge forwards;
	        animation: 0.5s cubic-bezier(0.785, 0.135, 0.15, 0.86) emerge forwards;
}

.leftemerge{
	-webkit-animation: 1s cubic-bezier(0.785, 0.135, 0.15, 0.86) leftemerge forwards;
	        animation: 1s cubic-bezier(0.785, 0.135, 0.15, 0.86) leftemerge forwards;
}

.leftemerge2{
	-webkit-animation: 1.3s cubic-bezier(0.785, 0.135, 0.15, 0.86) leftemerge2 forwards;
	        animation: 1.3s cubic-bezier(0.785, 0.135, 0.15, 0.86) leftemerge2 forwards;
}

.leftemerge3{
	-webkit-animation: 1.6s cubic-bezier(0.785, 0.135, 0.15, 0.86) leftemerge3 forwards;
	        animation: 1.6s cubic-bezier(0.785, 0.135, 0.15, 0.86) leftemerge3 forwards;
}

.rightemerge{
	-webkit-animation: 1.6s cubic-bezier(0.785, 0.135, 0.15, 0.86) rightemerge forwards;
	        animation: 1.6s cubic-bezier(0.785, 0.135, 0.15, 0.86) rightemerge forwards;
}

.main{
	height: 80%;
	width: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: start;
	    justify-content: flex-start;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    overflow-x: auto;
	-webkit-overflow-scrolling: touch;
	-ms-overflow-style: -ms-autohiding-scrollbar;
}

.main::-webkit-scrollbar { 
    display: none; 
}

.item{
	-ms-flex: 0 0 auto;
	    flex: 0 0 auto;
	height: 100%;
	width: 300px;
	margin-left:30px;
	cursor: pointer;
}

.name{
	height: 5%;
	width: 100%;
	font-size: 0.9em;
	margin: 5px;
	text-align: left;
}
.place{
	height: 5%;
	width: 100%;
	font-size: 0.8em;
	margin: 0 0 5px 5px;
	text-align: left;
}
.item img{
	height: 70%;
	width: 100%;
	-o-object-fit: cover;
	   object-fit: cover;
}

.date{
	height: 5%;
	width: 100%;
	color: #333;
	font-size: 0.8em;
	margin: 5px;
	text-align: left;
}

a:visited{
	color: black;
}

@media (max-width:1000px){
	.title{
		font-size: 4em;
	}
	.item img{
		height: 75%;
	}
}

@media (max-width:800px){
	.title{
		font-size: 2em;
	}
	.item img{
		height: 85%;
	}
}



